body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: Georgia, serif;

}
.navbar-brand img {
  width: 140px;
  height: 30px;
}
.navbar {
  background-color: #1f3a93;
  
}

.dropdown-item:hover{
  background-color: rgb(87, 84, 84);
}


.nav-item:hover{
  background-color: rgb(44, 123, 196);
}

.nav-link:hover {
  color: lightgrey !important;
}

.nav-link {
  color: white !important;
  font-size: 16px;
  
}

.navbar-nav > li > .dropdown-menu a {
  color: #ffffff;
  font-size: 15px;
  
}
.navbar-nav > li .active {
  font-weight: bold;
  text-decoration: underline;
  background-color: transparent;
  
}
@media (max-width: 992px) {
  .nav-link {
    text-align: center;
  }
}

.dropdown-menu {
  font-weight: bold;
  font-size: 12px;
  background-color: rgb(44, 123, 196);
  
}
#myBtnRecherche {
  color: white;
  font-family: Georgia, serif;
  border: 1px solid white;
  font-weight: bold;
  text-decoration: underline;
}
#myBtnRecherche:hover {
  color: lightgray !important;
  background-color: transparent;
  border: 1px solid lightgray !important;
}

a {
  text-decoration: none;
}

#mybtn {
  border: 2px solid white;
  background-color: transparent;
}
.mytoggler .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255, 255, 255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}
#mybtn.active.focus,
#mybtn.active:focus,
#mybtn.focus,
#mybtn:active.focus,
#mybtn:active:focus,
#mybtn:focus {
  outline: solid white 1px;
  box-shadow: none;
}

.header-inner{
  font-weight: bold;
  text-decoration: underline;
  background-color: transparent;
  font-family: Georgia, serif;
  margin-top: -7px;
  margin-bottom: -6px;
}

input[type=text] {
  color: white;
  font-family: Georgia, serif;
  border: 1px solid white;
  font-weight: bold;
  text-decoration: underline;
}


.box{
    width: 100%;
    background-color: #ECF8F6;
    margin: auto;
    color: #000000;;
    display: flex;
    align-items: center;
    justify-content: left;
    border-radius: 20px;
    position: relative;
    border: #ffffff solid 4px;
    margin-top: 50px;
}
.box p h1{
    font-size: 40px;
    font-weight: bolder;
}

a{
    text-decoration: none;
    color: black;
}


.hi{
  padding: 5px;
  background-color: #ECF8F6;
  /*display: flex;*/
  flex-wrap: wrap;
  justify-content: space-around;
  color: black;
  margin: auto;
  border-radius: 20px;
  counter-reset: li-counter;
  
}


.card {
    position: relative;
    /*display: flex;*/
    background-color: #F1E8B8;
    min-width: 0;
    word-wrap: break-word;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
    /*min-width: 0;
    max-width: 350px;*/
    /*width: 350px;
    height: 200px;*/
    margin:  auto;
    font-weight: bolder;
    font-family: Georgia, serif;
    font-size: larger;
    text-align: center;
    outline : black solid 2px ;
    cursor: grab;
  }
  .card-body {
    flex: 1 1 auto;
    padding: 2rem 1rem;
    text-align: center;
    border: 1px solid black;
    border-radius: 2px;
    cursor: grab;
  }
  .card-body h3{
    font-weight: bold;
    font-size: 25px;
  }
  .card-footer {
    color: black;
    /*background-color: #6cd6cc;*/
    border: 1px black solid;
    border-top: 1px solid rgba(0, 0, 0, 0.125);
    padding: 0px;
    
  }
.card-divider1{
  width: 100%;
  cursor: grab;
  background-color: #D6955B;
  border-right: #000000 1px solid;
  border-left: #000000 1px solid;
}
.card-divider2{
  width: 100%;
  cursor: grab;
  background-color: #FEEAA1;
  border-right: #000000 1px solid;
  border-left: #000000 1px solid;
}
.card-divider3{
  width: 100%;
  cursor: grab;
  background-color: #fad065;
  border-right: #000000 1px solid;
  border-left: #000000 1px solid;
}
.card-divider1:hover{
  background-color: #6cd6cc;
}
.card-divider2:hover{
  background-color: #6cd6cc;
}
.card-divider3:hover{
  background-color: #6cd6cc;
}

.card::before{
    position: absolute;
    top: -0.2em;
    left: -0.4em;
    width: 1.4em;
    height: 1.2em;
    font-size: 30px;
    line-height: 1.2;
    font-weight: bold;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color: #6cd6cc;
    transform: rotate(-20deg);
    -ms-transform: rotate(-20deg);
    -webkit-transform: rotate(-20deg);
    z-index: 99;
    overflow: hidden;
    content: counter(li-counter);
    counter-increment: li-counter;
    
  }


